body{
    margin:auto
}

#main{
    margin-left: 2%;
    margin-right: 2%;
}
#search{
    background-image: url('https://i.ibb.co/RcBcmdg/hp01.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-color:red;
    background-size: 100% 550px;
    width:100%;
    height: 550px;
}

#logoofweb1{
    height: 50px;
    background-color: rebeccapurple;
    
}
#logoofweb1 img{
    height: 50px;
    width: 210px;
    margin-left: 10px;
}
#logoofweb2{
    height: 50px;
    background-color: blue;
    
}
#login{
    margin-left: 0%;
    border: 1px;
    width: 100%;
    display: inline-block;
    margin-top: 10px;
    height: 50px;

}


#loginsignuplocated1{
    color:white;
    display: inline-block;
    font-size:18px;
    border:none;
    margin-left: 42%;
    text-decoration: none;

}
#loginsignuplocated2{
    text-decoration: none;
    display: inline-block;
    color:white;
    margin-left: 5%;
    font-size:18px;
    border: 1px solid whitesmoke;
    box-shadow: 0px 3px 15px white;
    padding: 10px;
}
#login a:hover{
    background-color:blue;
    transform: scale(1.1)
}
#logo{
    font-size:80px;
    color:red;
    text-align: center;
    background-color:white;
    height:150px;
    margin-top: 40px;
    width:150px;
    margin-left: 42%;
    border-radius: 50%;
}

#logo > span {
    font-weight: bold;
    position: absolute;
    margin-left: -35px;
    margin-top: 20px
}

#heading{
    text-align: center;
    color:white;
    font-size:76px;
    margin-top:30px;
}
#heading:hover{
    color:blueviolet;
}
#dropdown{
    width:60%;
    border-radius: 35%;
    margin-left: 28%;
    margin-top: 3%;
}

.signupbuttoncenter button{
    height: 55px;
    width: 170px;
    margin-left: 390px;
    border: none;
    font-size:18px;
    display: inline-block;
    background-color:blue;
    color:white;
}
.signupbuttoncenter button:hover{
    transform: scale(1.1)
}
select{
    width:25%;
    text-align: center;
    color:black;
    font-size:13px;
    background-color: whitesmoke;
    height:50px;
}
select:hover{
    color:blueviolet;
    background-color:lightgoldenrodyellow
}

#dropdown option{
    color: red;
    font: 15px;
    text-align: left;
}
#restaurants{
    width:50%;
}



@media screen and (max-width:1600px){
    body{
        background-color:lightgoldenrodyellow
    }
}

@media screen and (max-width:1400px){
    body{
        background-color: lightsalmon
    }
}

@media screen and (max-width:1200px){
    body{
        background-color: whitesmoke;
    }

}

@media screen and (max-width:1000px){
    body{
        background-color: lightslategrey;
        
    }
    #heading{
        font-size:68px;
    }
    #loginsignuplocated1{
        margin-left:30%;
    }
    .signupbuttoncenter button{
        margin-left:38%;
        height:50px;
        width: 160px;
        font-size: 20px;
    }
}

@media screen and (max-width:800px){
    body{
        background-color: lightpink;
    }       
    #heading{
        font-size:65px;
    }
    #loginsignuplocated1{
        margin-left:25%;
    }
    .signupbuttoncenter button{
        margin-left:38%;
        height:40px;
        width: 150px;
        font-size: 18px;
    }
}

@media screen and (max-width:600px){
    body{
        background-color: lightgreen;
    }
    #heading{
        font-size:50px;
    }
    #loginsignuplocated1{
        margin-left:4%;
    }
    #loginsignuplocated2{
        text-decoration: none;
        display: inline-block;
        color:white;
        margin-left: 5%;
        font-size:14px;
    }
    #logoofweb1 img{
        height: 40px;
        width: 170px;
        margin-left: 10px;
    }
    .signupbuttoncenter button{
        margin-left:38%;
        height:40px;
        width: 130px;
        font-size: 15px;
    }

}

